import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import {
  Box,
  Grid,
  Typography,
  Breadcrumbs,
  Link,
  Container,
  Paper,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  SelectChangeEvent,
  useMediaQuery,
  Tab,
  Tabs,
  Divider,
} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import { useTheme } from '@mui/material/styles';
import { useQuery } from 'react-query';
import { NavigateNext as NavigateNextIcon } from '@mui/icons-material';

import ProductGrid from '../components/products/ProductGrid';
import { motorService } from '../services/api';

const CatalogPage: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const location = useLocation();
  const navigate = useNavigate();
  const queryParams = new URLSearchParams(location.search);

  //    URL
  const categoryParam = queryParams.get('category') || '';
  const manufacturerParam = queryParams.get('manufacturer') || '';
  const searchQuery = queryParams.get('search') || '';
  const sortParam = queryParams.get('sort') || 'price_asc';
  const pageParam = parseInt(queryParams.get('page') || '1');
  const typeParam = queryParams.get('type') || 'all';

  //   
  const [currentPage, setCurrentPage] = useState(pageParam);
  const [sortBy, setSortBy] = useState(sortParam);
  const [motorType, setMotorType] = useState(typeParam);
  
  //  
  const { data: categoriesData, isLoading: categoriesLoading } = useQuery(
    'categories',
    motorService.getMotorCategories
  );

  //    
  const getQueryVariables = () => {
    return {
      page: currentPage,
      keyword: searchQuery || undefined,
      category: categoryParam || undefined,
      manufacturer: manufacturerParam || undefined,
      sortBy: sortBy,
      type: motorType !== 'all' ? motorType : undefined
    };
  };

  //  
  const {
    data: motorsData,
    isLoading: motorsLoading,
    error: motorsError,
    refetch: refetchMotors,
  } = useQuery(['motors', getQueryVariables()], () => 
    motorService.getAllMotors(getQueryVariables())
  );

  //  URL   
  useEffect(() => {
    const params = new URLSearchParams();
    
    if (categoryParam) params.set('category', categoryParam);
    if (manufacturerParam) params.set('manufacturer', manufacturerParam);
    if (searchQuery) params.set('search', searchQuery);
    params.set('sort', sortBy);
    if (currentPage > 1) params.set('page', currentPage.toString());
    if (motorType !== 'all') params.set('type', motorType);
    
    navigate(`/catalog?${params.toString()}`, { replace: true });
  }, [
    categoryParam,
    manufacturerParam,
    sortBy,
    currentPage,
    searchQuery,
    motorType,
  ]);

  //   
  const handleSortChange = (event: SelectChangeEvent) => {
    setSortBy(event.target.value);
    setCurrentPage(1); //       
  };

  //    
  const handleMotorTypeChange = (event: React.SyntheticEvent, newValue: string) => {
    setMotorType(newValue);
    setCurrentPage(1); //       
  };

  //   
  const handlePageChange = (page: number) => {
    setCurrentPage(page);
  };

  return (
    <Container maxWidth="xl">
      {/*   */}
      <Breadcrumbs
        separator={<NavigateNextIcon fontSize="small" />}
        aria-label="breadcrumb"
        sx={{ my: 2 }}
      >
        <Link component={RouterLink} to="/" color="inherit">
          
        </Link>
        <Typography color="text.primary"></Typography>
        {categoryParam && !categoriesLoading && categoriesData && (
          <Typography color="text.primary">
            {categoriesData.find((cat: any) => cat._id === categoryParam)?.name || ''}
          </Typography>
        )}
      </Breadcrumbs>

      {/*  */}
      <Typography 
        variant="h4" 
        component="h1" 
        gutterBottom 
        fontWeight="bold"
        sx={{ mb: 3 }}
      >
        {searchQuery
          ? ` : ${searchQuery}`
          : categoryParam && !categoriesLoading && categoriesData
            ? categoriesData.find((cat: any) => cat._id === categoryParam)?.name || ' '
            : ' '}
      </Typography>

      {/*     */}
      <Box sx={{ width: '100%', mb: 4 }}>
        <Tabs
          value={motorType}
          onChange={handleMotorTypeChange}
          textColor="primary"
          indicatorColor="primary"
          aria-label="motor types tabs"
          sx={{ mb: 2 }}
        >
          <Tab value="all" label=" " />
          <Tab value="refurbished" label=" " />
          <Tab value="new" label=" " />
          <Tab value="used" label=" " />
        </Tabs>
        <Divider />
      </Box>

      {/*    */}
      <Box sx={{ mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'flex-end', mb: 2 }}>
          <FormControl size="small" sx={{ minWidth: 200 }}>
            <InputLabel></InputLabel>
            <Select value={sortBy} onChange={handleSortChange} label="">
              <MenuItem value="price_asc"> </MenuItem>
              <MenuItem value="price_desc"> </MenuItem>
              <MenuItem value="rating_desc"> </MenuItem>
              <MenuItem value="createdAt_desc"> </MenuItem>
            </Select>
          </FormControl>
        </Box>

        {/*  */}
        <ProductGrid
          products={motorsLoading ? [] : (motorsData?.motors || [])}
          loading={motorsLoading}
          error={motorsError}
          totalPages={motorsData?.totalPages || 1}
          currentPage={currentPage}
          onPageChange={handlePageChange}
        />
      </Box>

      {/*   */}
      {motorType === 'refurbished' && (
        <Paper elevation={1} sx={{ p: 3, mt: 4 }}>
          <Typography variant="h5" gutterBottom fontWeight="bold">  </Typography>
          <Typography variant="body1" paragraph>
                       . 
                  .
          </Typography>
          <Typography variant="h6" gutterBottom fontWeight="bold">  :</Typography>
          <ul>
            <li>  50%    </li>
            <li> 12 </li>
            <li>   </li>
            <li>   </li>
          </ul>
        </Paper>
      )}
    </Container>
  );
};

export default CatalogPage; 